import Layout from "../components/Layout";
import Map from "../components/Map";
import Restlist from "../components/Restlist";
import React, {useState} from "react"


const Index = props => {


let [values, setValues] = useState({
    all: [
        {
            title:"Juliet's", 
            name:"Juliet's", 
            position:{lat:"30.261653", lng:"-97.760059"},
            url: href="http://www.juliet-austin.com/"
        }, 
        {
          title:"Intero",
          name: "Intero",
          position:{lat:"30.253737", lng:"-97.714451"}
        }, 
        {
          title:"Le Politique",
          name: "Le Politique",
          position:{lat:"30.265560", lng:"-97.749093"}
        }

    ], 
    vegan: [], 
    mixed: []
})

  return <Layout>
    <link
      rel="stylesheet"
      href="https://bootswatch.com/4/journal/bootstrap.min.css"
    />
    <div className="container">
      <h1>Welcome to Vegan Gems</h1>
      <br />
      <button
        id="veganMap"
        type="button"
        className="btn btn-success"
        style={{ marginRight: 40, marginLeft: 20, marginBottom: 5 }}
      >
        Completly Vegan
      </button>
      <button
        id="mixedMap"
        type="button"
        className="btn btn-success"
        style={{ marginBottom: 5 }}
      >
        Mixed
      </button>
    </div>
    <div className="container">
      <div className="row" style={{ height: 600 }}>
        <Restlist />
        <Map markers={values}/>
      </div>
    </div>
  </Layout>
};

export default Index;
